import React from 'react'
import PropTypes from 'prop-types'
import { compose, branch, renderComponent } from 'recompose'
import { DateInput, TimeInput, DateTimeInput, DatesRangeInput } from 'semantic-ui-calendar-react'
import { Form, Header, Segment, Item } from 'semantic-ui-react'

const withFormWrapper = WrappedComponent => {
  const InnerComponent = ({ selectorType, present, formGuid, title, value, setNote, setValue, ...props }) => (
    <Segment basic className='BDAFormField'>
      <Header as='h3'>{title}</Header>
      <Form.Field>
        <WrappedComponent
          {...props}
          value={value || ''}
          onChange={(e, { value }) => setValue(value)} />
      </Form.Field>
    </Segment>
  )
  InnerComponent.displayName = `/components/DatePicker`
  InnerComponent.propTypes = {
    name: PropTypes.string,
    title: PropTypes.string,
    value: PropTypes.string,
    placeholder: PropTypes.string,

    iconPosition: PropTypes.string,
    popupPosition: PropTypes.string,
    closable: PropTypes.bool,
    valueSet: PropTypes.func
  }
  InnerComponent.defaultProps = {
    name: 'DATEPICKER',
    iconPosition: 'left',
    popupPosition: 'bottom left',
    closable: true,
    value: ''
  }
  return InnerComponent
}

const Present = ({ title, value }) => (
  <Item>
    <Item.Content>
      <Item.Header as='h4'>{title}</Item.Header>
      <Item.Description>
        {value || <h4 style={{ color: 'red' }}>Not Entered</h4>}
      </Item.Description>
    </Item.Content>
  </Item>
)
Present.propTypes = DateInput.propTypes

export const DatePicker = compose(
  branch(({ present }) => !!present, renderComponent(Present)),
  withFormWrapper
)(DateInput)
export const DateTimePicker = compose(
  branch(({ present }) => !!present, renderComponent(Present)),
  withFormWrapper
)(DateTimeInput)
export const TimePicker = compose(
  branch(({ present }) => !!present, renderComponent(Present)),
  withFormWrapper
)(TimeInput)
export const DateRangePicker = compose(
  branch(({ present }) => !!present, renderComponent(Present)),
  withFormWrapper
)(DatesRangeInput)
