import React from 'react'
import PropTypes from 'prop-types'
import { Button } from 'semantic-ui-react'
import { withCreateStructure } from '../../../graphql/queries'

const AddButton = ({ createStructure }) => (
  <Button
    className='addButton'
    icon='plus'
    circular
    color='green'
    onClick={(e) => {
      e.preventDefault()
      createStructure({ body: '{}' })
    }}
  />
)

AddButton.propTypes = {
  createStructure: PropTypes.func
}

export default withCreateStructure(AddButton)
