import React from 'react'
import PropTypes from 'prop-types'
import { Step, Segment, Header, Divider, Responsive, Sidebar, Menu, Icon, Form, Dimmer, Button } from 'semantic-ui-react'
import { compose, withState, mapProps, pure } from 'recompose'
import { withReduxHome, withFormState } from '../../../redux/withRedux'
import withFieldData from '../../../graphql/withFieldData'
import './TheFormComplex.css'

const TheFormComplex = ({
  activeStep, sections, stepHeaders, sidebarVisible,
  getValueProps,
  setSidebarVisible,
  setActiveStep,
  setValue,
  setNote
}) => (
  <div style={{ height: '100%' }}>
    <TopbarEditing
      activeStep={activeStep}
      sidebarVisible={sidebarVisible}
      sections={sections}
      setSidebarVisible={setSidebarVisible}
      setActiveStep={setActiveStep} />
    <Sidebar.Pushable as='div'>
      <Sidebar
        as={Menu}
        animation='overlay'
        icon='labeled'
        inverted
        // onHide={this.handleSidebarHide}
        vertical
        visible={sidebarVisible}
        width='wide' >
        <Step.Group attached='top' style={{ marginTop: '40px' }}
          vertical
          ordered size='mini'
          fluid>
          {stepHeaders}
        </Step.Group>
      </Sidebar>

      <Sidebar.Pusher style={{ marginTop: '40px', marginBottom: '80px' }}>
        <Dimmer.Dimmable as={Segment} attached blurring>
          <Dimmer active={sidebarVisible} onClick={() => setSidebarVisible(false)} />
          <Responsive maxWidth='800'>
            <p>{sections[activeStep] && sections[activeStep].description}</p>
            <Divider />
          </Responsive>
          <Form>
            {sections.map((item, idx) => (
              <div key={`stepComp${idx}`} style={{ display: (activeStep === idx) || (!activeStep && idx === 0) ? '' : 'none' }}>
                <div key={`sectionlabel-${idx}`}>
                  {item.fields.map(({ component: FieldComponent, name, ...props }, idx) => (
                    <FieldComponent key={`field-${idx}`}
                      {...props}
                      {...getValueProps(name)}
                    />
                  ))}
                </div>
              </div>
            ))}
          </Form>
        </Dimmer.Dimmable>
      </Sidebar.Pusher>
    </Sidebar.Pushable>
    <Menu icon style={{ borderRadius: 0 }} fixed='bottom' inverted color='blue'>
      <Menu.Menu position='right'>
        {
          activeStep < 1 ? null : (
            <Menu.Item onClick={() => {
              setActiveStep(activeStep - 1)
              setSidebarVisible(false)
            }
            }>
              <Icon name='arrow circle left' />Previous Step
            </Menu.Item>
          )
        }
        {
          activeStep >= sections.length - 1 ? null : (
            <Menu.Item onClick={() => {
              setActiveStep(activeStep + 1)
              setSidebarVisible(false)
            }
            }>
                      Next Step<Icon name='arrow circle right' />
            </Menu.Item>
          )
        }
      </Menu.Menu>
    </Menu>
  </div>
)

TheFormComplex.propTypes = {
  activeStep: PropTypes.number,
  sections: PropTypes.array,
  sidebarVisible: PropTypes.bool,
  setSidebarVisible: PropTypes.func,
  setActiveStep: PropTypes.func,
  stepHeaders: PropTypes.arrayOf(PropTypes.node),
  // From recompose
  getValueProps: PropTypes.func,
  setValue: PropTypes.func,
  setNote: PropTypes.func
}

export default compose(
  withFormState,
  withFieldData,
  pure,
  withState('activeStep', 'setActiveStep', 0),
  withState('sidebarVisible', 'setSidebarVisible', false),
  mapProps(initProps => {
    const confirmStep = {
      title: 'Confirmation',
      description: 'Make sure everything is right',
      Component: () => (
        <div className='presentForm'>
          {initProps.sections.map(({ Component }, idx) => <Component present key={`presentComponent${idx}`} />)}
          <ConfirmMenu />
        </div>
      )
    }
    const newSections = [...initProps.sections, confirmStep] //

    const stepHeaders = newSections.map(({ title, description, complete }, idx) => (
      <Step link key={idx}
        active={idx === initProps.activeStep}
        completed={!!complete} onClick={() => {
          initProps.setActiveStep(idx)
          initProps.setSidebarVisible(false)
        }
        }>
        <Step.Content>
          <Step.Title>{title}</Step.Title>
          <Step.Description>{description}</Step.Description>
        </Step.Content>
      </Step>
    ))

    return {
      ...initProps,
      steps: [...newSections],
      stepHeaders: [...stepHeaders] // confirmHeader
    }
  })
)(TheFormComplex)

const ConfirmMenu = withReduxHome(({ editing: { guid }, backToHome }) => (
  <Menu icon style={{ borderRadius: 0 }}>
    <Menu.Menu position='right'>
      <Menu.Item>
        <Button content='Cancel' color='red' icon='ban' onClick={() => {
          // deleteStructure(guid)
          backToHome()
        }} />
      </Menu.Item>
      <Menu.Item>
        <Button content='Save' color='green' icon='save' onClick={backToHome} />
      </Menu.Item>
    </Menu.Menu>
  </Menu>
))

const TopbarEditing = withReduxHome(({ backToHome, activeStep, sidebarVisible, sections, setSidebarVisible, setActiveStep }) => (
  <Menu className='theFormComplexMenu' fixed='top' icon inverted style={{ borderRadius: 0 }} color='blue'>
    <Menu.Item onClick={backToHome} color='black'>
      <Icon name='home' />
    </Menu.Item>
    <Menu.Item name='bars' onClick={() => setSidebarVisible(!sidebarVisible)}>
      <Icon name='bars' />
    </Menu.Item>
    <Menu.Item style={{ maxWidth: '48%' }}>
      <Header as='div' style={{ fontSize: '0.8rem' }} inverted>Step {activeStep + 1}: {sections[activeStep].title}</Header>
    </Menu.Item>
    <Menu.Menu position='right'>
      {
        activeStep < 1 ? null : (
          <Menu.Item onClick={() => {
            setActiveStep(activeStep - 1)
            setSidebarVisible(false)
          }
          }>
            <Icon name='arrow circle left' />
          </Menu.Item>
        )
      }
      {
        activeStep >= sections.length - 1 ? null : (
          <Menu.Item onClick={() => {
            setActiveStep(activeStep + 1)
            setSidebarVisible(false)
          }}>
            <Icon name='arrow circle right' />
          </Menu.Item>
        )
      }
    </Menu.Menu>
  </Menu>
))
