import React from 'react'
import PropTypes from 'prop-types'
import ReactMapGL, { Marker } from 'react-map-gl'

import { AutoSizer } from 'react-virtualized'
import { Segment, Header, Dimmer, Loader, Message, Icon, Grid, Input, Item } from 'semantic-ui-react'
import { compose, withHandlers, mapProps, branch, renderComponent } from 'recompose'
import mapStyle from './LocationPicker.style.json'
import 'mapbox-gl/dist/mapbox-gl.css'

const LocationPicker = ({ title, loading, message, error, viewport, mapboxApiAccessToken, onViewportChange }) => {
  let icon = <Icon name='circle notched' loading />
  if (!loading) icon = <Icon name={error ? 'check times' : 'check circle'} />
  // console.log('RENDER', viewport)
  return (
    <Segment className='BDAFormField'>
      <Header as='h3'>{title}</Header>
      <Grid columns={2}>
        <Grid.Row>
          <Grid.Column>
            <Input fluid label='Lat:' placeholder='49.29949' value={viewport.latitude} />
          </Grid.Column>
          <Grid.Column>
            <Input fluid label='Lng:' placeholder='-123.34234' value={viewport.longitude} />
          </Grid.Column>
        </Grid.Row>
      </Grid>
      <div style={{ height: 400 }}>
        <Dimmer active={loading}>
          <Loader />
        </Dimmer>
        <AutoSizer>
          {({ height, width }) => loading ? null : (
            <ReactMapGL
              height={height}
              width={width}
              mapStyle={mapStyle}
              {...viewport}
              onViewportChange={onViewportChange}
              mapboxApiAccessToken={mapboxApiAccessToken}
            >
              {console.log('HERE', { loading, height, width })}
              <Marker key={`marker-1`}
                longitude={viewport.longitude}
                latitude={viewport.latitude}
                offsetLeft={-20} offsetTop={-10}
              >
                <Icon style={{ color: '#FFFFFF', textShadow: '0px 0px 2px #000000' }}
                  name='map marker alternate'
                  size='huge' />
              </Marker>
              {/* <Popup tipSize={5}
                anchor='top'
                longitude={viewport.longitude}
                latitude={viewport.latitude} /> */}
            </ReactMapGL>
          )
          }
        </AutoSizer>
      </div>
      <Message positive={!loading && !error} negative={error} icon>
        {icon}
        <Message.Content>
          <p>{error || message}</p>
        </Message.Content>
      </Message>

    </Segment>
  )
}

LocationPicker.propTypes = {
  title: PropTypes.string,
  viewport: PropTypes.object,
  mapboxApiAccessToken: PropTypes.string,
  onViewportChange: PropTypes.func,
  loading: PropTypes.bool,
  error: PropTypes.string,
  message: PropTypes.string
}
LocationPicker.defaultProps = {
  onViewportChange: () => console.log('onViewportChange NOT SET')
}

const withGeolocation = (WrappedComponent) =>
  class Geolocation extends React.Component {
    state = {
      loading: true,
      message: 'Retrieving location data...'
    }
    static propTypes = {
      viewport: PropTypes.object,
      onViewportChange: PropTypes.func
    }

    async componentWillMount () {
      const geolocation = navigator.geolocation
      const location = await new Promise(resolve => {
        if (!geolocation) {
          resolve({
            state: {
              error: 'Not Supported'
            }
          })
        }

        geolocation.getCurrentPosition(
          ({ coords: { latitude, longitude } }) => {
            resolve({
              viewport: Object.assign({}, this.props.viewport, {
                latitude,
                longitude
              }),
              state: {
                loading: false,
                message: 'Retrieved Location Data'
              }
            })
          },
          () => {
            resolve({
              state: {
                loading: false,
                error: 'Error retrieving location data from device.'
              }
            })
          })
      })
      // console.log('SETSTATE', { ...location })
      if (location.viewport) this.props.onViewportChange(location.viewport)
      this.setState(location.state)
    }

    render () {
      return <WrappedComponent {...this.props} {...this.state} />
    }
  }

const Present = ({ title, value }) => (
  <Item>
    <Item.Content>
      <Item.Header as='h4'>{title || 'Geolocation'}</Item.Header>
      <Item.Description>
        <pre><code>{JSON.stringify(value, null, 2)}</code></pre>
      </Item.Description>
    </Item.Content>
  </Item>
)

Present.propTypes = LocationPicker.propTypes

export default compose(
  withHandlers({
    onViewportChange: ({ value, setValue }) => newVal => {
      console.log('GCD', { value, setValue, newVal })
      setValue(newVal)
    }
  }),
  mapProps(props => ({
    ...props,
    viewport: props.value || {
      latitude: 49.29949,
      longitude: -123.1528795,
      zoom: 17
    }
  })),
  branch(({ present }) => !!present, renderComponent(Present)),
  withGeolocation
)(LocationPicker)
