import React from 'react'
import PropTypes from 'prop-types'
import { List } from 'semantic-ui-react'
import StructureListItem from './StructureListItem'
import Placeholder from '../../../misc/Placeholder'

const StructureList = ({ structures }) => (
  <div className='structureList'>
    { !structures || Object.keys(structures).length === 0
      ? <Placeholder minHeight={400} icon='list' title='No Structures Found' />
      : null }
    <List divided selection>
      {structures.map((structure, idx) => (
        <StructureListItem structure={structure} key={`structItem${structure.id}`} />
      ))}
    </List>
  </div>
)

StructureList.propTypes = {
  structures: PropTypes.array
}

export default StructureList
