import React from 'react'
import PropTypes from 'prop-types'
// import _ from 'lodash'
import { Dropdown, Segment, Header, Item } from 'semantic-ui-react'
import Note from './Note'
import { compose, branch, renderComponent, mapProps } from 'recompose'

const DropDownQuestion = ({
  title, formGuid, hasNote, dispatch, note, notePrompt, present, setValue, setNote, ...dropdownProps
}) => (
  <Segment basic className='BDAFormField'>
    <Header as='h3'>{title}</Header>
    <Dropdown {...dropdownProps} onChange={(e, { value }) => setValue(value)} />
    {!hasNote ? null
      : <Note
        label={notePrompt}
        value={note}
        setValue={setNote}
        modalTitle={`${title}: ${notePrompt}`}
      />}
  </Segment>
)

DropDownQuestion.propTypes = {
  title: PropTypes.string,
  dropdownProps: PropTypes.object,

  setValue: PropTypes.func,

  // Now the note stuff
  note: PropTypes.string,
  notePrompt: PropTypes.string,
  hasNote: PropTypes.bool,
  setNote: PropTypes.func
}

const Present = ({ title, value, options, note }) => (
  <Item>
    <Item.Content>
      <Item.Header as='h4'>{title}</Item.Header>
      <Item.Description>
        {printValues(value, options)}
      </Item.Description>
      {note ? <Item.Extra>Note: <em>{note}</em></Item.Extra> : null }
    </Item.Content>
  </Item>
)
function printValues (value, options) {
  if (Array.isArray(value) && value.length > 0) {
    return (
      <ul>
        {
          value.map((val, idx) => {
            let text = val
            try {
              text = options.find(op => op.value === val).text
            }
            catch (e) {}
            return (<li key={`item-${idx}`}>{text}</li>)
          })
        }
      </ul>
    )
  }
  else if (value && !Array.isArray(value)) {
    return options.find(op => op.value === value).text
  }
  else {
    return <h4 style={{ color: 'red' }}>Not Entered</h4>
  }
}

Present.propTypes = DropDownQuestion.propTypes

export default compose(
  branch(({ present }) => !!present, renderComponent(Present)),
  // Wrap setvalue because it can be multiples
  mapProps(({ options, multiple, value, ...otherProps }) => {
    let newVal
    let newOptions = []
    // dropdowns need a default state
    if (multiple) {
      newVal = Array.isArray(value) ? value : []
      const optionvals = options.map(op => op.value)
      const extraVals = newVal.filter(val => optionvals.indexOf(val) === -1)
      newOptions = [...options, ...extraVals.map(v => ({ text: v, value: v }))]
    }
    else {
      newVal = value || ''
      newOptions = options
    }

    return {
      ...otherProps,
      multiple,
      options: newOptions,
      value: newVal
    }
  })
)(DropDownQuestion)
