import React from 'react'
import PropTypes from 'prop-types'
import moment from 'moment'
import Swipeable from 'react-swipeable'
import { withDeleteStructure } from '../../../../graphql/queries'
import { withReduxHome } from '../../../../redux/withRedux'
import { List, Sidebar, Menu, Icon, Segment, Header, Confirm, Label } from 'semantic-ui-react'
import { compose, withStateHandlers, withProps } from 'recompose'

const StructureListItem = ({
  structure: { id, lastedit, body }, color,
  sidebarVisible, onSwipedRight, onSwipedLeft, editClick, itemClick, localOnly,
  deleteConfirmVisible, deleteClick, deleteCancel, deleteConfirm
}) => (
  <Sidebar.Pushable as={List.Item}>
    <Sidebar
      style={{ overflow: 'hidden' }}
      animation='push'
      as={Menu}
      icon='labeled'
      inverted
      direction='right'
      color='red'
      width='very thin'
      visible={sidebarVisible === SIDEBARS.DELETE} >
      <Menu.Item onClick={deleteClick} style={{ minWidth: '4em' }}>
        <Icon name='trash' /> Delete
      </Menu.Item>
    </Sidebar>
    <Sidebar
      style={{ overflow: 'hidden' }}
      animation='push'
      as={Menu}
      icon='labeled'
      inverted
      direction='left'
      color='blue'
      width='very thin'
      visible={sidebarVisible === SIDEBARS.EDIT} >
      <Menu.Item onClick={editClick} style={{ minWidth: '4em' }}>
        <Icon name='edit' /> Edit
      </Menu.Item>
    </Sidebar>
    <Sidebar.Pusher>
      <Swipeable onSwipedLeft={onSwipedLeft} onSwipedRight={onSwipedRight}>
        <Segment style={{ minHeight: 85 }} onClick={itemClick} basic >
          { localOnly === true ? <Label icon='refresh' as='a' color='yellow' corner /> : null}
          <Header inverted onClick={() => {}}>
            {getHeader(JSON.parse(body))}
          </Header>
          {getMeta(JSON.parse(body))}
          {console.log(localOnly)}
        </Segment>
      </Swipeable>
    </Sidebar.Pusher>
    <Confirm
      header='Delete Structure?'
      open={deleteConfirmVisible}
      onCancel={deleteCancel}
      onConfirm={deleteConfirm}
      size='large'
    />
  </Sidebar.Pushable>
)

StructureListItem.propTypes = {
  structure: PropTypes.object,
  color: PropTypes.string,
  localOnly: PropTypes.bool,
  // recompose
  sidebarVisible: PropTypes.number,
  deleteConfirmVisible: PropTypes.bool,
  onSwipedLeft: PropTypes.func,
  onSwipedRight: PropTypes.func,
  itemClick: PropTypes.func,
  editClick: PropTypes.func,
  deleteClick: PropTypes.func,
  deleteCancel: PropTypes.func,
  deleteConfirm: PropTypes.func
  // from redux
  // viewStructure: PropTypes.func
}

const SIDEBARS = {
  NONE: null,
  DELETE: 1,
  EDIT: -1
}

export default compose(
  withReduxHome,
  withProps(({ structure: { id } }) => {
    const localOnly = id.indexOf('pending') === 0
    let color = null
    if (localOnly === true) color = 'yellow'
    // else if (!uploading && !uploaded && uploadFailed) color = 'red'
    return { color, localOnly }
  }),
  withDeleteStructure,
  withStateHandlers(
    {
      sidebarVisible: SIDEBARS.NONE,
      deleteConfirmVisible: false
    },
    {
      itemClick: ({ sidebarVisible }, { structure: { id }, viewStructure }) => (e) => {
        console.log('click', e, sidebarVisible, id)
        if (sidebarVisible !== SIDEBARS.NONE) return { sidebarVisible: SIDEBARS.NONE }
        else {
          viewStructure(id)
        }
      },
      editClick: ({ sidebarVisible }, { structure: { id }, editStructure }) => (e) => {
        console.log('click EDIT', e, sidebarVisible, id)
        editStructure(id)
      },
      onSwipedLeft: ({ sidebarVisible }) => () => {
        switch (sidebarVisible) {
          case SIDEBARS.NONE:
            return { sidebarVisible: SIDEBARS.DELETE }
          case SIDEBARS.EDIT:
            return { sidebarVisible: SIDEBARS.NONE }
          default:
            break
        }
      },
      onSwipedRight: ({ sidebarVisible }) => () => {
        switch (sidebarVisible) {
          case SIDEBARS.NONE:
            return { sidebarVisible: SIDEBARS.EDIT }
          case SIDEBARS.DELETE:
            return { sidebarVisible: SIDEBARS.NONE }
          default:
            break
        }
      },
      closeMenu: () => (e) => {
        e.stopPropagation() // Make sure we don't accidentally trigger  click
        return { sidebarVisible: SIDEBARS.NONE }
      },
      deleteClick: () => () => ({ deleteConfirmVisible: true }),
      deleteCancel: () => () => ({ deleteConfirmVisible: false, sidebarVisible: SIDEBARS.NONE }),
      deleteConfirm: (state, { deleteStructure, structure }) => () => {
        deleteStructure({ id: structure.id })
        return { deleteConfirmVisible: false, sidebarVisible: SIDEBARS.NONE }
      }
    }
  )
)(StructureListItem)

