import React from 'react'
import PropTypes from 'prop-types'
import { Form, Checkbox, Header, Segment, Item } from 'semantic-ui-react'
import Note from './Note'
import { compose, withHandlers, renderComponent, branch } from 'recompose'

const CheckboxQuestion = ({ title, options, hasNote, note, notePrompt, setNote, setValue }) => (
  <Segment basic className='BDAFormField'>
    <Header as='h3'>{title}</Header>
    {options.map(({ label, value }, idx) => (
      <Form.Field key={idx}>
        <Checkbox label={label} value={value} onChange={() => setValue(value)} />
      </Form.Field>
    ))}
    {!hasNote ? null
      : <Note
        label={notePrompt}
        value={note}
        setValue={setNote}
        modalTitle={`${title}: ${notePrompt}`}
      />}
  </Segment>
)

CheckboxQuestion.propTypes = {
  title: PropTypes.string,
  options: PropTypes.arrayOf(PropTypes.object),
  // Now the note stuff
  note: PropTypes.string,
  notePrompt: PropTypes.string,
  hasNote: PropTypes.bool,
  setNote: PropTypes.func,

  setValue: PropTypes.func
}

const Present = ({ title, value, options, note }) => (
  <Item>
    <Item.Content>
      <Item.Header as='h4'>{title}</Item.Header>
      <Item.Description>
        {value && Array.isArray(value) && value.length > 0
          ? printValues(value, options)
          : <h4 style={{ color: 'red' }}>Not Entered</h4>}
      </Item.Description>
      {note ? <Item.Extra>Note: <em>{note}</em></Item.Extra> : null }
    </Item.Content>
  </Item>
)
function printValues (value, options) {
  return (
    <ul>
      {value.map((val, idx) => (<li key={`item-${idx}`}>{options.find(op => op.value === val).label}</li>)) }
    </ul>
  )
}

Present.propTypes = CheckboxQuestion.propTypes

export default compose(
  branch(({ present }) => !!present, renderComponent(Present)),
  // Wrap setvalue because it can be multiples
  withHandlers({
    setValue: ({ value, setValue }) => (newVal) => {
      let newValues = []
      if (Array.isArray(value)) newValues = value.slice(0)

      if (newValues.indexOf(newVal) < 0) newValues.push(newVal)
      else newValues = newValues.splice(newValues.indexOf(newVal), 1)
      setValue(newValues)
    }
  })
)(CheckboxQuestion)
