import React from 'react'
import PropTypes from 'prop-types'
import { Button } from 'semantic-ui-react'
import { withCreateStructure } from '../../../graphql/queries'

const RefreshStockButton = ({ createStructure }) => (
  <Button
    className='refreshButton'
    icon='refresh'
    content='Clear and refresh stock structures'
    fluid
    color='red'
    onClick={(e) => {
      // READ THE STOCK ENTRIES FROM A JSON FILE
      const data = require('../views/data.json')
      Object.values(data.structures).forEach(({ fields }) => {
        createStructure({ body: JSON.stringify(fields) })
      })
      console.log('refresh', { data, createStructure })
    }}
  />
)

RefreshStockButton.propTypes = {
  createStructure: PropTypes.func
}

export default withCreateStructure(RefreshStockButton)
