import React from 'react'
import PropTypes from 'prop-types'
import { AutoSizer } from 'react-virtualized'
import { Icon, Header, Image } from 'semantic-ui-react'
import { pure } from 'recompose'
import './Placeholder.css'

const Placeholder = pure(({
  autoHeight, autoWidth, aspect, width, height,
  className, json, description, title, icon, showSize
}) => {
  let finalHeight = height || autoHeight
  let finalWidth = width || autoWidth

  if (aspect) {
    finalHeight = aspect * finalWidth
  }

  const minSize = Math.min(finalHeight, finalWidth)
  const minIconFontSize = minSize * 6
  const iconStyle = {
    fontSize: `${minIconFontSize}%`
    // marginTop: `${(height - minIconFontSize) / 2}px`
  }

  const widthText = `${finalWidth}px`
  const heightText = `${finalHeight}px`
  const style = {
    maxWidth: widthText,
    width: widthText,
    height: heightText
  }
  const classes = ['myPlaceholder'].concat(className.split(' ')).join(' ')
  return (
    <Image className={classes} style={style}>
      {icon ? <Icon name={icon} style={iconStyle} /> : null}
      <div className='centered'>
        {title ? <Header>{title.toString()}</Header> : null}
        {showSize ? (
          <div className='sizeText'>
            {`${widthText} X ${heightText}`}
          </div>
        ) : null}
        {description ? <p className='description'>{description}</p> : null}
        {json ? (
          <div className='json'><pre><code>
            {JSON.stringify(json, null, 2)}
          </code></pre></div>
        ) : null}
      </div>
    </Image>
  )
})

Placeholder.propTypes = {
  // from the autosizer component
  autoHeight: PropTypes.number,
  autoWidth: PropTypes.number,
  className: PropTypes.string,
  // These are passed in (maybe)
  height: PropTypes.number,
  width: PropTypes.number,
  aspect: PropTypes.number,
  json: PropTypes.object,
  description: PropTypes.string,
  icon: PropTypes.string,
  showSize: PropTypes.bool,
  title: PropTypes.string,
  // from recompose
  size: PropTypes.object
}
Placeholder.defaultProps = {
  className: ''
}

const SizerWrapper = ({ minHeight, ...props }) => (
  <div style={{
    minHeight,
    height: minHeight ? '100%' : null
  }}>
    <AutoSizer>
      {({ height, width }) => (
        <Placeholder autoHeight={height} autoWidth={width} {...props} />
      )}
    </AutoSizer>
  </div>
)
SizerWrapper.displayName = 'Placeholder'
export default SizerWrapper
