import React from 'react'
import ReactDOM from 'react-dom'
import Homepage from './components/BDAMobile/views/Homepage'
import registerServiceWorker from './registerServiceWorker'
import { Provider } from 'react-redux'
import store from './redux/store'
import config from './config'

import AWSAppSyncClient from 'aws-appsync'
import { Rehydrated } from 'aws-appsync-react'
import { ApolloProvider } from 'react-apollo'

// Amplify
import Amplify, { Auth } from 'aws-amplify'
import { withAuthenticator } from 'aws-amplify-react'

// Import some global CSS styles we use everywhere
import 'semantic-ui-css/semantic.css'
import 'react-vis/dist/style.css'
import './index.css'

// Amplify init
Amplify.configure(config.Auth)

// AppSync client instantiation
const client = new AWSAppSyncClient({
  url: config.GraphQLEndpoint,
  region: config.Auth.region,
  auth: {
    type: 'AMAZON_COGNITO_USER_POOLS',
    // Get the currently logged in users credential.
    jwtToken: async () => (await Auth.currentSession()).getAccessToken().getJwtToken()
  },
  // Amplify uses Amazon IAM to authorize calls to Amazon S3. This provides the relevant IAM credentials.
  complexObjectsCredentials: () => Auth.currentCredentials()
})

const AppWithAuth = withAuthenticator(Homepage, false)

// REDUX then apollo then auth
ReactDOM.render(
  <Provider store={store}>
    <ApolloProvider client={client}>
      <Rehydrated>
        <AppWithAuth />
      </Rehydrated>
    </ApolloProvider>
  </Provider>,
  document.getElementById('root'))

// This is what makes a PWA
registerServiceWorker()

function addToHomeScreen () {
  const a2hsBtn = document.querySelector('.ad2hs-prompt') // hide our user interface that shows our A2HS button
  a2hsBtn.style.display = 'none' // Show the prompt
  deferredPrompt.prompt() // Wait for the user to respond to the prompt
  deferredPrompt.userChoice
    .then(choiceResult => {
      if (choiceResult.outcome === 'accepted') console.log('User accepted the A2HS prompt')
      else console.log('User dismissed the A2HS prompt')

      deferredPrompt = null
    })
}

function showAddToHomeScreen () {
  const a2hsBtn = document.querySelector('.ad2hs-prompt')
  a2hsBtn.style.display = 'block'
  a2hsBtn.addEventListener('click', addToHomeScreen)
}

let deferredPrompt
window.addEventListener('beforeinstallprompt', e => {
  // Prevent Chrome 67 and earlier from automatically showing the prompt
  e.preventDefault()
  // Stash the event so it can be triggered later.
  deferredPrompt = e

  showAddToHomeScreen()
})

// Detects if device is on iOS
const isIos = () => {
  const userAgent = window.navigator.userAgent.toLowerCase()
  return /iphone|ipad|ipod/.test(userAgent)
}
// Detects if device is in standalone mode
const isInStandaloneMode = () => ('standalone' in window.navigator) && (window.navigator.standalone)

// Checks if should display install popup notification:
if (isIos() && !isInStandaloneMode()) {
  console.log('I AM on IOS and I am NOT in standalone mode.')
  // this.setState({ showInstallMessage: true })
}
