import React from 'react'
import PropTypes from 'prop-types'
import { Segment, Button, Header, Item } from 'semantic-ui-react'
import { compose, withHandlers, mapProps, branch, renderComponent } from 'recompose'
import './FloodBehavior.css'

const GRADIENT = [
  'green',
  'yellow',
  'orange',
  'red'
]

const FloodBehavior = ({ label, value: { location: loc, behavior }, setLoc, setBehavior }) => (
  <Segment basic className='floodBehavior  BDAFormField'>
    <Header as='h3'>{label}</Header>
    <Button.Group fluid toggle className={'behave ' + (behavior && behavior.length > 0 ? 'set' : '')}>
      {['Intact', 'Minor', 'Major', 'Blowout'].map((bText, idx) => (
        <Button
          key={`behav${idx}`}
          onClick={() => {
            if (bText === 'Intact' || bText === 'Blowout') setLoc([])
            setBehavior(bText)
          }
          }
          active={behavior && behavior === bText}
          color={GRADIENT[idx]}>
          {bText}
        </Button>
      ))}
    </Button.Group>
    {
      !behavior || behavior === 'Intact' || behavior === 'Blowout' ? null
        : <LocationGroup loc={loc} behavior={behavior} setLoc={setLoc} />

    }
  </Segment>
)

FloodBehavior.propTypes = {
  label: PropTypes.string,
  value: PropTypes.shape({
    loc: PropTypes.array,
    behavior: PropTypes.string
  }),
  setLoc: PropTypes.func,
  setBehavior: PropTypes.func
}

const LocationGroup = ({ loc, setLoc }) => (
  <div style={{ marginTop: '2em' }}>
    <Header as='h4'>Location: (select all that apply)</Header>
    <Button.Group fluid toggle>
      {['Left', 'Center', 'Basal', 'Right'].map((bText, idx) => {
        return (
          <Button
            key={`loca${idx}`}
            size='large'
            onClick={() => {
              let newLoc = []
              if (loc && Array.isArray(loc)) newLoc = loc.splice(0)
              if (newLoc.indexOf(bText) === -1) newLoc.push(bText)
              else newLoc.splice(newLoc.indexOf(bText), 1)
              setLoc(newLoc)
            }}
            active={loc && loc.indexOf(bText) > -1}>
            {bText}
          </Button>
        )
      })}
    </Button.Group>
  </div>
)

LocationGroup.propTypes = {
  loc: PropTypes.array,
  setLoc: PropTypes.func
}

const Present = ({ label, value: { location: loc, behavior } }) => (
  <Item>
    <Item.Content>
      <Item.Header as='h4'>{label}</Item.Header>
      <Item.Description>
        <ul>
          <li>{behavior || <h4 style={{ color: 'red' }}>Behavior Not Entered</h4>}</li>
          {
            !behavior || behavior === 'Intact' || behavior === 'Blowout' ? null
              : <li>{loc ? loc.join(', ') : <h4 style={{ color: 'red' }}>Location Not Entered</h4>}</li>
          }
        </ul>
      </Item.Description>
    </Item.Content>
  </Item>
)

Present.propTypes = FloodBehavior.propTypes

export default compose(
  // Wrap setvalue because it can be multiples
  withHandlers({
    setLoc: ({ value, setValue }) => (newVal) => {
      if (!value) setValue({ behavior: null, location: newVal })
      else setValue({ ...value, location: newVal })
    },
    setBehavior: ({ value, setValue }) => (newVal) => {
      if (!value) setValue({ behavior: newVal, location: null })
      else setValue({ ...value, behavior: newVal })
    }
  }),
  mapProps((props) => ({
    ...props,
    value: props.value ? props.value : { behavior: null, location: null }
  })),
  branch(({ present }) => !!present, renderComponent(Present))
)(FloodBehavior)
