import React from 'react'
import PropTypes from 'prop-types'
import Note from './Note'
import { Image, Header, Segment, Card, Responsive, List, Modal, Button, Item, Icon } from 'semantic-ui-react'
import { compose, withStateHandlers, mapProps, branch, renderComponent } from 'recompose'
import Placeholder from '../../misc/Placeholder'
import './CardListPicker.css'

const CardListPicker = ({ title, options, hasNote, note, notePrompt, placeholder, value, setNote, toggleValue, modalOpen, setModal }) => {
  const items = options.map((op, idx) => (
    <CardListPickerItem
      key={idx}
      toggleValue={toggleValue}
      selected={value && Array.isArray(value) && value.indexOf(op.value) > -1}
      placeholder={placeholder}
      {...op}
    />))

  return (
    <Segment className='BDAFormField'>
      <Header as='h3'>{title}</Header>
      <Modal closeIcon id='cardListModal' open={modalOpen} onClose={() => setModal(false)} centered={false}>
        <Modal.Header>Select a Photo</Modal.Header>
        <Modal.Content>
          <Responsive as='div' maxWidth={499}>
            <Card.Group itemsPerRow={3} centered>
              {items}
            </Card.Group>
          </Responsive>
          <Responsive as='div' minWidth={500}>
            <Card.Group itemsPerRow={4} centered>
              {items}
            </Card.Group>
          </Responsive>
        </Modal.Content>
        <Modal.Actions>
          <Button color='green' onClick={() => setModal(false)}>
            <Icon name='checkmark' /> Ok
          </Button>
        </Modal.Actions>
      </Modal>
      <Button onClick={() => setModal(true)}>Choose...</Button>
      <List onClick={() => setModal(true)}>
        {options.filter(op => value.indexOf(op.value) === 0).map((op, idx) => <CardListListItem
          key={idx}
          selected={value.indexOf(op.value) === 0}
          placeholder={placeholder}
          {...op} />)}
      </List>
      {!hasNote ? null
        : <Note
          label={notePrompt}
          value={note}
          setValue={setNote}
          modalTitle={`${title}: ${notePrompt}`}
        />}
    </Segment>
  )
}

CardListPicker.propTypes = {
  title: PropTypes.string,
  value: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.string
  ]),
  toggleValue: PropTypes.func,
  setModal: PropTypes.func,
  options: PropTypes.array,
  hasNotes: PropTypes.bool,
  modalOpen: PropTypes.bool,
  placeholder: PropTypes.object, // This is for design only

  // Now the note stuff
  note: PropTypes.string,
  notePrompt: PropTypes.string,
  hasNote: PropTypes.bool,
  setNote: PropTypes.func
}

const CardListListItem = ({ image, text, subText, placeholder }) => (
  <List.Item>
    {
      placeholder
        ? <Placeholder {...placeholder} minHeight={50} />
        : <Image src={image} width='100' />
    }
    <List.Content>
      <List.Header>{text}</List.Header>
      {subText && subText.length > 0 ? <List.Description>{subText}</List.Description> : null }
    </List.Content>
  </List.Item>
)
CardListListItem.propTypes = {
  image: PropTypes.string,
  text: PropTypes.string,
  subText: PropTypes.string,
  placeholder: PropTypes.object // This is for design only
}

const CardListPickerItem = ({ image, text, value, subText, selected, placeholder, toggleValue }) => (
  <Card link onClick={() => toggleValue(value)} className={selected ? 'active' : ''}>
    {
      placeholder
        ? <Placeholder {...placeholder} minHeight={200} />
        : <Image src={image} width={200} />
    }
    <Card.Content>
      <Card.Header>{text}</Card.Header>
      {/* <Card.Meta>{tool.context}</Card.Meta> */}
      {subText && subText.length > 0 ? <Card.Description>{subText}</Card.Description> : null }
    </Card.Content>
  </Card>
)

CardListPickerItem.propTypes = {
  image: PropTypes.string,
  value: PropTypes.string,
  text: PropTypes.string,
  subText: PropTypes.string,
  toggleValue: PropTypes.func,
  selected: PropTypes.bool,
  placeholder: PropTypes.object // This is for design only
}

const Present = ({ title, options, value, placeholder }) => (
  <Item>
    <Item.Content>
      <Item.Header as='h4'>{title}</Item.Header>
      <Item.Description>
        <List >
          {options.filter(op => value.indexOf(op.value) === 0).map((op, idx) => <CardListListItem
            key={idx}
            selected={value.indexOf(op.value) === 0}
            placeholder={placeholder}
            {...op} />)}
        </List>
      </Item.Description>
    </Item.Content>
  </Item>
)

Present.propTypes = CardListPicker.propTypes

export default compose(
  withStateHandlers(
    {
      modalOpen: false
    },
    {
      toggleModal: ({ modalOpen }) => () => ({ modalOpen: !modalOpen }),
      setModal: () => (val) => ({ modalOpen: val }),
      toggleValue: (state, { value, multiple, setValue }) => val => {
        if (multiple) {
          let newValues = []
          if (Array.isArray(value)) newValues = value.slice(0)

          const index = newValues.indexOf(val)
          if (index < 0) newValues.push(val)
          else newValues.splice(index, 1)
          setValue(newValues)
        }
        else {
          setValue(val)
          return { modalOpen: false }
        }
      }
    }
  ),
  mapProps(props => ({
    ...props,
    value: props.value || []
  })),
  branch(({ present }) => !!present, renderComponent(Present))
)(CardListPicker)
