import React from 'react'
import PropTypes from 'prop-types'

const Track = ({ trackNum, step, source, target, getTrackProps }) => {
  const classes = ['sliderTrack']
  const dynamicStyle = {
    left: `${source.percent}%`,
    width: `${target.percent - source.percent}%`
  }
  if (trackNum === 2) {
    classes.push('limiter')
    // console.log('YOWZA', target.percent, source.percent)
    if (target.percent < 100 + step) {
      dynamicStyle.left = `${source.percent - step}%`
      dynamicStyle.width = `${target.percent - source.percent + step}%`
    }
  }
  else if (trackNum === 0) {
    classes.push('progress')
  }

  return (
    <div className={classes.join(' ')} style={dynamicStyle}
      // {...getTrackProps()}
    />
  )
}

Track.propTypes = {
  trackNum: PropTypes.number,
  step: PropTypes.number,
  source: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    percent: PropTypes.number.isRequired
  }).isRequired,
  target: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    percent: PropTypes.number.isRequired
  }).isRequired,
  getTrackProps: PropTypes.func.isRequired
}

export default Track
