import gql from 'graphql-tag'
import { graphql } from 'react-apollo'
import { v4 as uuid } from 'uuid'

const QueryListStructures = gql`
  query listBDAPOCS {
    listBDAPOCS {
      items {
        id
        body
        lastEdit
      }
    }
  }
`

const QueryGetStructure = gql`
  query getBDAPOC($id:ID!){
    getBDAPOC(id:$id) {
      id
      body
      lastEdit
    }
  }
`

const MutationCreateStructure = gql`
  mutation createBDAPOC($input: CreateBDAPOCInput!) {
    createBDAPOC(input: $input) {
      id
      body
      lastEdit
    }
  }
`

const MutationUpdateStructure = gql`
  mutation updateBDAPOC($input: UpdateBDAPOCInput!) {
    updateBDAPOC(input: $input) {
      id
      body
      lastEdit
    }
  }
`

const MutationDeleteStructure = gql`
  mutation deleteBDAPOC($input: DeleteBDAPOCInput!) {
    deleteBDAPOC(input: $input) {
      id
      body
      lastEdit
    }
  }
`

export const withGetStructure = graphql(QueryGetStructure, {
  options: ({ pollInterval, editing }) => ({
    fetchPolicy: 'cache-and-network',
    // pollInterval: pollInterval || 5000
    variables: { id: editing ? editing.guid : null }
  }),
  props: props => ({
    // debug: console.log('this get structure', props),
    structure: props.data.getBDAPOC ? {
      ...props.data.getBDAPOC,
      body: JSON.parse(props.data.getBDAPOC.body)
    } : null
  })
})

export const withStructuresList = graphql(QueryListStructures, {
  options: ({ pollInterval }) => ({
    fetchPolicy: 'cache-and-network',
    pollInterval: pollInterval || 5000
  }),
  props: props => ({
    // debug: console.log('this query', props),
    structures: props.data.listBDAPOCS ? props.data.listBDAPOCS.items : []
  })
})

export const withCreateStructure = graphql(
  MutationCreateStructure,
  {
    options: {
      update: (proxy, { data: { createBDAPOC } }) => {
        const query = QueryListStructures
        const data = proxy.readQuery({ query })
        data.listBDAPOCS.items = [
          ...data.listBDAPOCS.items.filter(struct => !createBDAPOC || struct.id !== createBDAPOC.id),
          createBDAPOC
        ]
        console.log('update', { data, createBDAPOC })
        proxy.writeQuery({ query, data })
      }
    },
    props: ({ ownProps, mutate }) => ({
      createStructure: structure => mutate({
        variables: {
          input: {
            ...structure,
            lastEdit: Math.round(new Date().getTime() / 1000)
          }
        },
        optimisticResponse: () => ({
          __typename: 'Mutation',
          createBDAPOC: {
            ...structure,
            id: `pending${uuid()}`,
            lastEdit: Math.round(new Date().getTime() / 1000),
            __typename: 'BDAPOC'
          }
        })
      })
    })
  }
)

export const withDeleteStructure = graphql(
  MutationDeleteStructure,
  {
    options: {
      update: (proxy, { data: { deleteBDAPOC } }) => {
        const query = QueryListStructures
        const data = proxy.readQuery({ query })
        data.listBDAPOCS.items = data.listBDAPOCS.items
          .filter(struct => !deleteBDAPOC || struct.id !== deleteBDAPOC.id)
        proxy.writeQuery({ query, data })
      }
    },
    props: ({ ownProps, mutate }) => ({
      deleteStructure: structure => mutate({
        variables: { input: structure },
        optimisticResponse: () => ({
          __typename: 'Mutation',
          deleteBDAPOC: {
            ...structure,
            __typename: 'BDAPOC'
          }
        })
      })
    })
  }
)
