import React from 'react'
import PropTypes from 'prop-types'
import { Message, TextArea, Confirm, Form, Icon, Segment } from 'semantic-ui-react'
import { compose, withStateHandlers } from 'recompose'
import './Note.css'

const Note = ({ name, value, open, setOpen, setValue, setTempVal, label, placeholder, modalTitle, modalDescription }) => (
  <div>
    <Trigger label={label} value={value} onClick={() => setOpen(true)} />
    <Confirm
      header={modalTitle}
      open={open}
      onCancel={() => setOpen(false)}
      onConfirm={setValue}
      content={(
        <Segment basic>
          <p>{modalDescription}</p>
          <Form>
            <TextArea name={name} placeholder={placeholder} rows={10} defaultValue={value} onChange={setTempVal} />
          </Form>
        </Segment>
      )}
    />
  </div>

)

Note.defaultProps = {
  name: 'NOTES',
  label: 'Add a note...',
  modalTtle: 'Add a note:'
}

Note.propTypes = {
  name: PropTypes.string,
  value: PropTypes.string,
  open: PropTypes.bool,
  label: PropTypes.string,
  modalTitle: PropTypes.string,
  placeholder: PropTypes.string,
  modalDescription: PropTypes.string,
  setOpen: PropTypes.func,
  setValue: PropTypes.func,
  setTempVal: PropTypes.func
}

const Trigger = ({ label, value, onClick }) => (
  <Message className='notesMessage' onClick={onClick}>
    <Message.Header>
      <Icon name='pencil' />{label}
    </Message.Header>
    {value && value.length > 0 ? (<span className='notesVal'>{getShortString(value, 66)}</span>) : ''}
  </Message>
)

Trigger.propTypes = {
  label: PropTypes.string,
  value: PropTypes.string,
  onClick: PropTypes.func
}

export default compose(
  withStateHandlers(
    {
      tempVal: '', // The value as it's changing
      open: false
    },
    {
      toggleOpen: ({ open }) => ({ open: !open }),
      setOpen: () => (open) => ({ open }),
      setTempVal: () => (e, { value }) => ({ tempVal: value }),
      setValue: ({ tempVal }, { setValue }) => () => {
        setValue(tempVal) // this goes to redux
        return ({ open: false })
      }
    }
  )
)(Note)

/**
 * Helper function to truncate a string
 */
function getShortString (theStr, len) {
  return theStr.length > len ? theStr.substring(0, len) + '...' : theStr
}
