import React from 'react'
import { Auth } from 'aws-amplify'
import PropTypes from 'prop-types'
import { Menu, Icon, Button } from 'semantic-ui-react'
import { withReduxHome } from '../../redux/withRedux'

const Topbar = ({ editing, backToHome }) => (
  <Menu icon inverted style={{ borderRadius: 0 }}>
    {editing && editing.guid
      ? <Menu.Item name='bars' onClick={() => backToHome()}>
        <Icon name='arrow left' />
      </Menu.Item>
      : null
    }
    <Menu.Item header>Project Homepage</Menu.Item>
    <Menu.Menu position='right'>
      <Menu.Item>
        <Button color='yellow' icon='lock open' inverted content='Log out' onClick={() => {
          Auth.signOut()
          // This is a hack to force the page to refresh
          window.location.replace(window.location)
        }} />
        {/* <Icon name='rese' /> */}
      </Menu.Item>
    </Menu.Menu>
  </Menu>
)

Topbar.propTypes = {
  editing: PropTypes.object,
  createStructure: PropTypes.func,
  // from redux
  backToHome: PropTypes.func
}

export default withReduxHome(Topbar)
