import React from 'react'
import PropTypes from 'prop-types'
import { Form, Header, Segment, Input, Item } from 'semantic-ui-react'
import { compose, branch, renderComponent } from 'recompose'

export const TextField = ({ label, placeholder, type, value, setValue }) => (
  <Segment basic className='BDAFormField'>
    <Header as='h3'>{label}</Header>
    <Form.Field>
      <Input type={type}
        defaultValue={value}
        placeholder={placeholder}
        onChange={(e, { value: newval }) => setValue(newval)} />
    </Form.Field>
  </Segment>
)

TextField.propTypes = {
  label: PropTypes.string,
  placeholder: PropTypes.string,
  type: PropTypes.string,

  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number
  ]),
  setValue: PropTypes.func
}
TextField.defaultProps = {
  setValue: () => console.log('setValue NOT SET')
}

const Present = ({ label, value }) => (
  <Item>
    <Item.Content>
      <Item.Header as='h4'>{label}</Item.Header>
      <Item.Description>
        {value || <h4 style={{ color: 'red' }}>Not Entered</h4>}
      </Item.Description>
    </Item.Content>
  </Item>
)

Present.propTypes = TextField.propTypes

export default compose(
  branch(({ present }) => !!present, renderComponent(Present))
)(TextField)
