import React from 'react'
import PropTypes from 'prop-types'
import { Segment, Divider, Header } from 'semantic-ui-react'
import Topbar from '../Topbar'
import { withFormState } from '../../../redux/withRedux'
import withFieldData from '../../../graphql/withFieldData'
import { compose, branch, renderNothing } from 'recompose'
// Import our form elements
import sections from '../sections'

/**
   * Same goes for the viewer
   */
const StructureViewer = ({ structure, getValueProps }) => (
  <div className='appContainer'>
    <Topbar />
    <Segment style={{ marginTop: 0, overflow: 'scroll' }} className='presentForm'>
      {sections.map((item, idx) => (
        <div className='section' key={`section-${idx}`}>
          <Header>{item.title}</Header>
          <div style={{ paddingLeft: '1.5rem' }}>
            {item.fields.map(({ component: Component, name, ...props }, idx) => (
              <Component key={`field-${idx}`} present name={name} {...props} {...getValueProps(name)} />
            ))}
            <Divider />
          </div>
        </div>
      ))}
    </Segment>
  </div>
)

StructureViewer.propTypes = {
  structure: PropTypes.object,
  getValueProps: PropTypes.func
}

export default compose(
  withFormState,
  withFieldData,
  branch(({ structure }) => !structure, renderNothing)
)(StructureViewer)
