import { compose, withHandlers, mapProps } from 'recompose'
import { actions } from './reducers'
import { connect } from 'react-redux'

const mapStateToFieldProps = (state, props) => {
  const { editing, structures } = state
  const { name } = props
  let formGuid = ''
  let currField = { value: '', note: '' }
  try {
    console.log('editing', { editing, structures })
    currField = structures[editing.guid].fields[name] || {}
    formGuid = editing.guid
  }
  catch (e) { }
  return {
    formGuid,
    value: currField.value,
    note: currField.note
  }
}

export const withFormState = compose(
  connect(mapStateToFieldProps), // Once connect() is composed `dispatch` is prop.
  // Make sure dispatch and formGuid never make it to the individual components
  mapProps(({ formGuid, ...otherProps }) => otherProps)
)

const mapStateToHomeProps = ({ editing }, props) => ({
  editing
})

/**
 * HOMEPAGE state
 */

export const withReduxHome = compose(
  connect(mapStateToHomeProps), // Once connect() is composed `dispatch` is prop.
  withHandlers({
    backToHome: ({ dispatch }) => () => {
      console.log('backToHome called')
      dispatch(actions.backToHome())
    },
    editStructure: ({ dispatch }) => (guid) => {
      console.log('editStructure called', guid)
      dispatch(actions.editStructure(guid))
    },
    viewStructure: ({ dispatch }) => (guid) => {
      console.log('viewStructure called', guid)
      dispatch(actions.viewStructure(guid))
    }
  })
)
