import React from 'react'
import PropTypes from 'prop-types'
import { Form, Radio, Header, Segment, Item } from 'semantic-ui-react'
import Note from './Note'
import { compose, branch, renderComponent } from 'recompose'

export const RadioQuestion = ({ title, name, options, value, hasNote, note, notePrompt, setValue, setNote }) => (
  <Segment basic className='BDAFormField'>
    <Header as='h3'>{title}</Header>
    {options.map(({ label, value: rVal }, idx) => (
      <Form.Field key={idx}>
        <Radio
          label={label}
          name={name}
          value={rVal}
          defaultChecked={value && rVal === value}
          onChange={() => setValue(rVal)}
        />
      </Form.Field>
    ))}
    {!hasNote ? null
      : <Note
        label={notePrompt}
        value={note}
        setValue={setNote}
        modalTitle={`${title}: ${notePrompt}`}
      />}
  </Segment>
)

RadioQuestion.propTypes = {
  title: PropTypes.string,
  options: PropTypes.arrayOf(PropTypes.object),
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number
  ]),
  setValue: PropTypes.func,

  // Now the note stuff
  note: PropTypes.string,
  notePrompt: PropTypes.string,
  hasNote: PropTypes.bool,
  setNote: PropTypes.func
}
RadioQuestion.defaultProps = {
  setValue: () => console.log('setValue NOT SET'),
  setNote: () => console.log('setNote NOT SET')
}

const Present = ({ title, value, options, note }) => (
  <Item>
    <Item.Content>
      <Item.Header as='h4'>{title}</Item.Header>
      <Item.Description>
        {value ? options.find(op => op.value === value).label
          : <h4 style={{ color: 'red' }}>Not Entered</h4>}
      </Item.Description>
      {note ? <Item.Extra>Note: <em>{note}</em></Item.Extra> : null }
    </Item.Content>
  </Item>
)

Present.propTypes = RadioQuestion.propTypes

export default compose(
  branch(({ present }) => !!present, renderComponent(Present))
)(RadioQuestion)
