/**
 * NOTE: This gets checked into Git. Nothing secret should go here
 */
export const AuthStates = {
  unknown: 'unknown', // This is the initial state
  unauthenticated: 'unauthenticated',
  signingOut: 'signingOut',
  signedOut: 'signedOut',
  signingIn: 'signingIn',
  signedIn: 'signedIn',
  mfaRequired: 'mfaRequired',
  newPasswordRequired: 'newPasswordRequired'
}

// Need to supply a mapbox token (it's not secret. Putting it in git is fine)
export const mapBoxToken = process.env.REACT_APP_MAPBOX_TOKEN

let domain = process.env.REACT_APP_DOMAIN
if (process.env.NODE_ENV && process.env.NODE_ENV === 'development') {
  domain = 'localhost'
}

export default {
  env: process.env.NODE_ENV,
  GraphQLEndpoint: process.env.REACT_APP_GRAPHQL,
  // Cognito stuff
  Auth: {
    // REQUIRED only for Federated Authentication - Amazon Cognito Identity Pool ID
    // identityPoolId: 'XX-XXXX-X:XXXXXXXX-XXXX-1234-abcd-1234567890ab',

    // REQUIRED - Amazon Cognito Region
    region: process.env.REACT_APP_AWS_REGION,

    // OPTIONAL - Amazon Cognito Federated Identity Pool Region
    // Required only if it's different from Amazon Cognito Region
    // identityPoolRegion: 'XX-XXXX-X',

    // OPTIONAL - Amazon Cognito User Pool ID
    userPoolId: process.env.REACT_APP_USER_POOL_ID,

    // OPTIONAL - Amazon Cognito Web Client ID (26-char alphanumeric string)
    userPoolWebClientId: process.env.REACT_APP_COGNITO_CLIENT,

    // OPTIONAL - Enforce user authentication prior to accessing AWS resources or not
    mandatorySignIn: true,

    // OPTIONAL - Configuration for cookie storage
    cookieStorage: {
      // REQUIRED - Cookie domain (only required if cookieStorage is provided)
      domain: `${domain}`,
      // OPTIONAL - Cookie path
      path: process.env.REACT_APP_COOKIEPATH || null,
      // OPTIONAL - Cookie expiration in days
      expires: 20,
      // OPTIONAL - Cookie secure flag (requires HTTP)
      secure: true
    }

    // OPTIONAL - customized storage object
    // storage: new MyStorage(),

    // OPTIONAL - Manually set the authentication flow type. Default is 'USER_SRP_AUTH'
    // authenticationFlowType: 'USER_PASSWORD_AUTH'
  }
}
