import { combineReducers } from 'redux'
export const actionConsts = {
  EDIT_STRUCTURE: 'EDIT_STRUCTURE',
  VIEW_STRUCTURE: 'VIEW_STRUCTURE',
  BACK_TO_HOME: 'BACK_TO_HOME'
}

/**
 * Actions are actually just really simple objects
 */
export const actions = {
  backToHome: () => ({ type: actionConsts.BACK_TO_HOME }),
  editStructure: (guid) => ({ type: actionConsts.EDIT_STRUCTURE, guid }),
  viewStructure: (guid) => ({ type: actionConsts.VIEW_STRUCTURE, guid })
}

const defaultState = {
  editing: null
}

const editing = (state = defaultState.editing, action) => {
  switch (action.type) {
    case actionConsts.EDIT_STRUCTURE:
      return {
        guid: action.guid,
        edit: true
      }
    case actionConsts.VIEW_STRUCTURE:
      return {
        guid: action.guid,
        edit: false
      }
    case actionConsts.BACK_TO_HOME:
      return null
    default:
      return state
  }
}

export default combineReducers({
  editing
})
